import React from "react";
import { Route, Switch } from "react-router-dom";
import Sidebar from "./components/Sidebar";
import Transcendence from "./components/projects/Transcendence";
import CleverAudio from "./components/projects/CleverAudio";
import EarthquakeGeoMapping from "./components/projects/EarthquakeGeoMapping";
import HealthRisks from "./components/projects/HealthRisks";
import Adventurously from "./components/projects/Adventurously";
import RandomQuoteMachine from "./components/projects/RandomQuoteMachine";
import MarkdownPreviewer from "./components/projects/MarkdownPreviewer";
import Calculator from "./components/projects/Calculator";
import Pomodoro from "./components/projects/Pomodoro";
import Home from "./components/Home";
import NoMatch from "./components/NoMatch";
import MahjongSpace from "./components/projects/MahjongSpace";


class PortfolioRoutes extends React.Component {


    render() {
        return (
            <div className="page-container">
                <Sidebar />

                <div className="viewport" id="viewport">

                    <Switch>
                        <Route exact={true} path="/" component={Home} />
                        <Route exact={true} path="/react-mahjong" component={MahjongSpace} />
                        <Route exact={true} path="/transcendence" component={Transcendence} />
                        <Route exact={true} path="/random-quote-machine" component={RandomQuoteMachine} />
                        <Route exact={true} path="/markdown-previewer" component={MarkdownPreviewer} />
                        <Route exact={true} path="/calculator" component={Calculator} />
                        <Route exact={true} path="/pomodoro" component={Pomodoro} />
                        <Route exact={true} path="/clever-audio" component={CleverAudio} />
                        <Route exact={true} path="/earthquake-geo-mapping" component={EarthquakeGeoMapping} />
                        <Route exact={true} path="/health-risks" component={HealthRisks} />
                        <Route exact={true} path="/adventurously" component={Adventurously} />
                        <Route component={NoMatch} />
                    </Switch>

                </div>
                <div className="resizer-mask" id="resizer-mask" />

            </div>
        );
    }

}

export default PortfolioRoutes;