import React from "react";

class NoMatch extends React.Component {
    render() {
        return (
            <div className="welcome">
                <div className="resizer-pulser-wrapper">
                    <div className="resizer-pulser-container">
                        <div className="resizer-pulser"></div>
                    </div>
                </div>
                <div className="no-match-box">
                    <h2>404</h2>
                    <p>Page not found.  Please double-check your spelling, or select a project from the navigation menu.</p>
                </div>
            </div>
        );
    }
}

export default NoMatch;