import React from "react";
// import { Link } from "react-router-dom";
import { NavLink } from "react-router-dom";

class Project extends React.Component {

    render() {
        let external, github, codepen;

        if (this.props.external) {
            external = (
                <a href={this.props.external} title="View Externally Deployed Project" target="_blank" rel="noopener noreferrer" className="project-links-external-link"><i className="fas fa-external-link-alt" /></a>
            );
        }

        if (this.props.github) {
            github = (
                <a href={this.props.github} title="View GitHub Repository" target="_blank" rel="noopener noreferrer" className="project-links-external-link"><i className="fab fa-github" /></a>
            );
        }

        if (this.props.codepen) {
            codepen = (
                <a href={this.props.codepen} title="View on CodePen" target="_blank" rel="noopener noreferrer" className="project-links-external-link"><i className="fab fa-codepen" /></a>
            );
        }

        return (
            <li className="project">
                <NavLink className="project-link" activeClassName="project-link-active" to={this.props.route} >
                    <h2>{this.props.title}</h2>
                    <p>{this.props.description}</p>
                </NavLink>
                <div className="project-links-external">
                    { external }
                    { github }
                    { codepen }
                </div>
            </li>
        );
    }
}

export default Project;