import React from "react";

class HerokuLoader extends React.Component {
    render() {
        return (
            <div className="heroku-loader-container">
                <div  className="heroku-loading">
                    <div className="heroku-loading-circle outer-circle" />
                </div>
                <div  className="heroku-loading">
                    <div className="heroku-loading-circle inner-circle" />
                </div>
                <div  className="heroku-loading">
                    <div className="heroku-loading-circle inner-inner-circle" />
                </div>
                <div className="heroku-notification">
                    <span className="heroku-notification-text">This app is deployed on Heroku for free.  Please be patient while it loads...</span>
                </div>
            </div>
        );
    }
}

export default HerokuLoader;